@use '../../styles/vars';
@use 'sass:color';

.post{
    width: 100%;
    margin: 0 auto vars.$gutter*3;
    @media( max-width: 769px ) {
        width: 100%;
    }
    &-header{
        width: 100%;
        text-align: center;
        position: relative;
        &-img{
            width:100%;
            height: 450px;
            object-fit: cover;
            //object-position: top;
            @media( max-width: 426px ) {
                height: 300px;
            }
        }
        &-title{
            display: inline-block;
            padding: vars.$gutter vars.$gutter*2;
            margin-top: - (vars.$gutter*3);
            font-size: vars.$gutter*3;
            font-weight: bold;
            text-transform: uppercase;
            @media( max-width: 426px ) {
                font-size: vars.$gutter*2;
            }
        }
    }
    &-info{
        text-align: center;
        padding: vars.$gutter;
        color: #999;
        font-style: oblique;
        &-date, &-author{
            font-weight: bold;
        }
        @media( max-width: 426px ) {
            font-size: vars.$gutter*0.9;
        }
    }
    &-content{
        width: 60%;
        margin: 0 auto;
        font-size: vars.$gutter*1.2;
        line-height: 1.2;
        text-align: justify;
        @media( max-width: 769px ) {
            width: 80%;
        }
        @media( max-width: 426px ) {
            width: 90%;
            font-size: vars.$gutter*1.1;
        }
        &-intro, &-outro{
            font-weight: bold;
            font-size: vars.$gutter*1.3;
            @media( max-width: 426px ) {
                font-size: vars.$gutter*1.2;
            }
        }
        &-intro{
            margin: vars.$gutter*2 0;
        }
        &-outro{
            margin-top: vars.$gutter*2;
        }
        &-context, &-contact{
            border-radius: 10px;
            margin: vars.$gutter*2 0;
            
            &-title{
                position: relative;
                top: -5px;
                padding: vars.$gutter;
                // background-color: vars.$background-secondary;
                // color: vars.$background-primary;
                text-transform: uppercase;
                font-weight: bold;
                
            }
            &-text{
                padding: vars.$gutter;
            }
        }
        &-context-title{
            float: left;
            left: -5px;
            margin-right: vars.$gutter;
            border-bottom-right-radius: 10px;
            border-top-left-radius: 10px;
            @media( max-width: 426px ) {
                float: none;
                margin: 0;
                display: inline-block;
            }
        }
        &-contact-title{
            float: right;
            right: -5px;
            margin-left: vars.$gutter;
            border-bottom-left-radius: 10px;
            border-top-right-radius: 10px;
            @media( max-width: 426px ) {
                float: none;
                display: inline-block;
                margin: 0;
                left: -5px;
                border-bottom-right-radius: 10px;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 0;
                border-top-right-radius: 0;
            }
        }
    }
}

.subtitle{
    font-size: vars.$gutter*1.3;
    font-weight: bold;
    text-transform: uppercase;
    margin: vars.$gutter*4 0 vars.$gutter;
}